.navbar .correct {
  -webkit-animation: .4s correct;
          animation: .4s correct;
}

.navbar .incorrect {
  -webkit-animation: .4s incorrect;
          animation: .4s incorrect;
}

@-webkit-keyframes correct {
  0% {
    color: white;
  }

  50% {
    color: #2e7d32;
  }

  100% {
    color: white;
  }
}

@keyframes correct {
  0% {
    color: white;
  }

  50% {
    color: #2e7d32;
  }

  100% {
    color: white;
  }
}

@-webkit-keyframes incorrect {
  0% {
    color: white;
  }

  50% {
    color: #c62828;
  }

  100% {
    color: white;
  }
}

@keyframes incorrect {
  0% {
    color: white;
  }

  50% {
    color: #c62828;
  }

  100% {
    color: white;
  }
}



